body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

h1,
h2,
h3 {
    margin: 0.5rem 0;
}

h1 {
    font-size: 1.5rem;
}

h2 {
    font-size: 1.2rem;
}

h3 {
    font-size: 1rem;
}

a {
    color: black;
    text-decoration: none;
}

.row {
    display: flex;
    justify-content: space-between;
}

.line {
    display: flex;
    display: table-row;
}

.col-1 {
    flex: 1;
}

.col-2 {
    flex: 1;
}

.col-3 {
    flex: 1;
}

.block {
    background-color: aliceblue;
    padding: 1rem;
    margin: 0.5rem;
    border-radius: 0.5rem;
}

.row.center {
    align-items: center;
}

img.small {
    max-width: 8rem;
}

button.block {
    font-size: 0.8rem;
    padding: 0.2rem;
    margin: 0.1rem;
    border-radius: 0.5rem;
    border: 0.1rem #444444 solid;
    background-color: paleturquoise;
    cursor: pointer;
}

button {
    font-size: 0.8rem;
    padding: 0.2rem;
    margin: 0.1rem;
    border-radius: 0.5rem;
    border: 0.1rem #444444 solid;
    background-color: paleturquoise;
    cursor: pointer;
}

button.add {
    background-color: paleturquoise;
    width: 1.5rem;
}

button.remove {
    background-color: #f04040;
    width: 1.5rem;
}

button.badge {
    background-color: #f04040;
    width: 1.5rem;
    border: none;
    color: aliceblue;
}

.text-right {
    text-align: right;
}

.row-block-center {
    text-decoration: none;
}



.checkout-button{
    font-size: 0.8rem;
    padding: 0.2rem;
    margin: 0.1rem;
    border-radius: 0.5rem;
    border: 0.1rem #444444 solid;
    background-color: paleturquoise;
    cursor: pointer;
    width: fit-content;
}

p {
    color: #f04040;
}